.Menu, .MenuTrigger {
    position: absolute;
    top: 2px;
    left: 2px;
    z-index: 1;

    padding: 10px;
    border: 1px solid #78AB46;
    background-color: white;

    width: 200px;
    height: 100%;

    box-sizing: border-box;
}

.MenuTrigger {
  width: 20px;
  height: auto;
  cursor: pointer;
}

.Menu--header {
    margin-top: 0;
    padding-top: 0;
}

.Menu ul {
    list-style-type: none;
    padding: 0;
}

.Menu--item {
    cursor: pointer;
    position: relative;
    padding-left: 5px;
    border-left: solid 4px transparent;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.Menu--item.active {
    border-left: solid 4px yellow;
    background-color: #f4f4f4;
}

.Menu--item button {
  float: right;
}
