html, body, #root, .Editor, .ProseMirror {
    width: 100%;
    height: 100%;
    caret-color: black;
}

.Editor {
    border: solid 1px black;
    overflow: auto;
    position: relative;
    background-color: #efefef;
}

.Editor--menu button.active {
  background-color: blue;
}

.ProseMirror, .Editor--menu, .Editor--stats, .Editor--agenda, .Editor--help {
    position: relative;
    left: 100px;
    box-sizing: border-box;
    max-width: 800px;
    height: auto;
    margin: 10px auto 0;
    padding: 10px 10px 10px 50px;
    outline: none;
    background-color: white;
}

.EditorWrapper {
  min-height: 100%;
}

:focus {
    outline: 0;
}

p {
    position: relative;
}

li p {
    display: inline;
}

li, p {
    margin: 0;
    padding: 0;
    line-height: 1.3;
}

h1, h2, h3, h4, h5 {
    position: relative;
}

.Editor ul {
    margin: 0 !important;
}

/* -------------------------------------------------------------------------- */
/* Decorations */

.bold {
    font-weight: 700;
}

.italic {
    font-style: italic;
}

.underline {
    text-decoration: underline;
}

.striketrough {
    text-decoration: line-through;
}

.highlight {
    background-color: #FFFF00;
    color: #aaa;
}

.tag {
    color: #efefef;
    background-color: #78AB46;
    cursor: pointer;
}

.hr {
    border-bottom: solid 1px;
    display: block;
    line-height: 1px;
    text-align: right;
}

.explication {
    color:#999;
}

.url {
    cursor: pointer !important;
    position: relative;
}

.url::before {
    content: 'Hold CMD and click to open';
    display: inline-block;
    width: 100%;
    padding-top: 5px;
    text-align: center;
    position: absolute;
    left: 0px;
    bottom: calc(100% - 18px);
    font-size: 8px;
    opacity: 0;
    transition: all 0.5s ease;
    cursor: pointer;
}

.url:hover::before {
    bottom: 100%;
    opacity: 1;
    background-color: #efefef;
}

.quote {
    color: white;
    display: inline-block;
    border-left: 4px solid #78AB46;
    padding: 1px;
}

.bullet {
    color: #78AB46;
}

.ordered {
    color: #78AB46;
}

.tableRow {
    color: green;
    font-family: monospace;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.code {
    z-index: 2;
    color: transparent;
}

.code-highlight {
    top: 0;
    left: 0;
    position: absolute;
    z-index: -2;

    width: 100%;
    margin: 0;
    line-height: 20px;

    color: blue;
    background-color: #f0f0f0;
    font-family: inherit;
}

.checkbox {
    color: transparent;
    display: inline-block;
    width: 1px;
    overflow: hidden;
    white-space: nowrap;
    vertical-align: middle;
}

/* -------------------------------------------------------------------------- */
/* Date */

.date {
    border-bottom: 2px dotted #78AB46;
}

.date--menu--trigger {
    font-size: 8px;
    padding: 0px;
    margin: 0;
    line-height: 17px;
}

/* -------------------------------------------------------------------------- */
/* Heading */

.heading {

}

.heading--hidden {
    display: inline-block;
    width: 0px;
    overflow: hidden;
    white-space: nowrap;
    vertical-align: middle;
}


/* -------------------------------------------------------------------------- */
/* Duration */

.duration {
    border-bottom: 2px dotted #78AB46;
}

.duration--menu {
    position: relative;
}

.duration--menu--trigger {
    font-size: 8px;
    padding: 0px;
    margin: 0;
    line-height: 17px;
}

.duration--menu--options {
    display: none;
    position: absolute;
    z-index: 2;
    top: 100%;
    left: 0;
    width: 60px;
    padding: 10px;
    border: 1px solid #78AB46;
    background-color: #efefef;
}

.duration--menu:hover .duration--menu--options {
    display: block;
    z-index: 2;
}

/* -------------------------------------------------------------------------- */
/* Counter */

.counter, .counter--hidden {
    display: inline-block;
    height: 20px;
    vertical-align: middle;
    font-family: monospace;
    text-align: center;
    box-sizing: border-box;
    vertical-align: middle;
}

.counter {
    border: 1px solid #78AB46;
    padding: 0 5px;
    user-select: none;
}

.counter--hidden {
    font-family: monospace;
    color: transparent;
    display: inline-block;
    width: 0px;
    line-height: 20px;
}

.counter--hidden.right {
    font-size: 10px;
    position: relative;
    left: -5px;
    background-color: blue;
}

.counter--left, .counter--right {
    vertical-align: middle;
    font-family: monospace;
    border: 1px solid #78AB46;
    cursor: pointer;
    background-color: white;
    display: inline-block;
    height: 20px;
    box-sizing: border-box;
}

.counter--right {
    margin-left: 5px;
    margin-right: 5px;
}

.counter--left {
    margin-right: 5px;
}

/* -------------------------------------------------------------------------- */
/* Indicators */

.indicator {
    z-index: 2;
    position: absolute;
    right: calc(100% + 10px);
    top: calc(50% - 7.5px);
    font-size: 15px;
    cursor: pointer;
}

/* -------------------------------------------------------------------------- */
/* TODO */

.todo {
    font-family: monospace;
    position: relative;
    left: -11px;
    color: transparent;
}

.todo-indicator.indicator {
    top: 0px;
    font-size: 10px;
    text-align: right;
    width: 150px;
    background-color: rgba(0, 0, 0, 0.05);
    padding: 10px;
    cursor: auto;
}

.todo-indicator.indicator .trigger {
    border: 1px solid transparent;
}

.todo-indicator.indicator .options {
    display: none;
}

.todo-indicator.indicator:hover .options {
    display: block;
    text-align: left;
}

.todo-indicator.indicator button {
    cursor: pointer;
    border: 1px solid #78AB46;
    margin: 2px;
}

/* -------------------------------------------------------------------------- */
/* Popup */

.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: #efefef;
    top: 0px;
    left: 0px;
    z-index: 10000;
}

.popup .content {
    width: 500px;
    min-height: 300px;
    margin: 100px auto;
    border: 1px solid #78AB46;
    background-color: white;
    position: relative;
}


.popup .centered {
    text-align: center;
}

.popup header {
    position: relative;
}

.popup header h2 {
    margin: 0;
    padding: 10px;
}

.popup header .close {
    position: absolute;
    top: 10px;
    right: 10px;
    border: 1px solid #78AB46;
}

.popup section {
    padding: 10px;
}

.popup footer {
  text-align: center;
  position: absolute;
  bottom: 10px;
  width: 100%;
}
